body{
display: flex;
position: absolute;
margin: 0;
padding: 0;
background-repeat: no-repeat;
background-size: cover;
justify-content: center;
align-items: center;
width: 100%;
height: 100%;
}
.wrapper{
position: relative;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
width: 7cm;
height: 7cm;
margin: 0cm;
padding: 0cm;
gap: 0.5cm;
background-color: transparent;
border-radius: 0;
border-style: none;
}
