.logoImg{
position: relative;
width: 1cm;
height: auto;
}
.infoText{
color: white;
}
.simpleNota{

}
.commonInput{
position: relative;
width: 7cm;
height: 1cm;
padding: 0cm;
color: grey;
text-align: center;
background-color: transparent;
border-style: none;
outline: none;
font-size: large;
color: white;
border-radius: 0.3cm;
z-index: 1;
box-shadow: inset 0 0 0 100px transparent;
border:1px solid;
border-color: gray;
}
.emailGroup{
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    background-color: transparent;

}
.pwdGroup{
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    background-color: transparent;
}
.emailLabel{
    position: absolute;
    color: gray;
    pointer-events: none;
    transition: all 0.2s ease;
    z-index: 2;
    padding: 0 5px;
}
.passwordLabel{
    position: absolute;
    color: gray;
    pointer-events: none;
    transition: all 0.2s ease;
    z-index: 2;
    padding: 0 5px;
}
.commonInput:focus + .passwordLabel,
.commonInput:not(:placeholder-shown) + .passwordLabel  {
  top: -15px;
  font-size: 14px;
  color: #FB847C;
  font-weight: bolder;
  background-color: transparent;
  z-index: 1111;
}
.commonInput:focus + .emailLabel,
.commonInput:not(:placeholder-shown) + .emailLabel  {
  top: -15px;
  font-size: 14px;
  color: #FB847C;
  font-weight: bolder;
  background-color: transparent;
  z-index: 1111;
}
.loginButton{
position: relative;
display: flex;
width: 7cm;
height: 1cm;
padding: 0cm;
margin: 0cm;
background-color: #CC34AD;
color: white;
border-style: none;
border-radius: 0.4cm;
transition: 0.3s ease-in-out;
justify-content: center;
align-items: center;  
}
.loginButton:hover{
    cursor: pointer;
    transition: 0.3s ease-in-out;
}
.forgotPwd{
color: gray;
text-decoration: none;
align-self: flex-end;
}
.forgotPwd:hover{
    cursor: pointer;
    text-decoration: underline;
}
.signupLink{
    color: #FB847C;
    text-decoration: none;
}
.signupLink:hover{
    cursor: pointer;
    text-decoration: underline;
}
.signupText{
color: white;
}
.loginH1{
color: white;
}
.loader {
    border: 3px solid rgba(255, 255, 255, 0.3); /* Light border */
    border-top: 3px solid white; /* White top border */
    border-radius: 50%; /* Circular shape */
    width: 16px; /* Small size for the loader */
    height: 16px; /* Small size for the loader */
    animation: spin 1s linear infinite; /* Spinning animation */
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); } /* Start at 0 degrees */
    100% { transform: rotate(360deg); } /* Complete rotation */
  }